import React from "react";
import styles from './index.module.scss'
import WorkCrypto from "../../assets/img/works-section/work-crypto.svg";
import WorkLine from "../../assets/img/works-section/works-line.svg";
import WorkQr from "../../assets/img/works-section/work-qr.svg";
import WorkSwitch from "../../assets/img/works-section/work-switch.svg";
import WorkSquad from "../../assets/img/works-section/work-squad.svg";
import WorkSwitchSecond from "../../assets/img/works-section/work-switch-second-menu.svg";
import WorkMoney from "../../assets/img/works-section/work-money.svg";
import WorkCryptoSecond from "../../assets/img/works-section/work-crypto-second-menu.svg";

class WorksSection extends React.Component {
    constructor(props) {
        super(props);
        this.state ={
            isAcceptCrypto: false
        }
    }
    onChangeMenu = (param) => {
        this.setState({
            isAcceptCrypto: param
        })
    };
    render() {
        return (
          <section className={styles.works_section}>
              <div className={styles.content_block}>
                  <div className={styles.caption}>How it works</div>
                  <div className={styles.para_block}>
                      <div className={this.state.isAcceptCrypto ? styles.para : `${styles.para} ${styles.active}`}
                           onClick={(e) => this.onChangeMenu(false)}>Accept crypto</div>
                      <div className={this.state.isAcceptCrypto ? `${styles.para} ${styles.active}` : styles.para}
                           onClick={(e) => this.onChangeMenu(true)}>Pay with crypto</div>
                  </div>
                  {!this.state.isAcceptCrypto
                  ?
                      <div className={styles.element_block}>
                          <div className={styles.element}>
                              <img className={styles.img} src={WorkCrypto} alt={""}/>
                              <div className={styles.element_text}>Customer chooses the coin</div>
                          </div>
                          <img className={styles.line} src={WorkLine} alt={""}/>
                          <div className={styles.element}>
                              <img className={styles.img} src={WorkQr} alt={""}/>
                              <div className={styles.element_text}>Scan QR code and make deposit</div>
                          </div>
                          <img className={styles.line} src={WorkLine} alt={""}/>
                          <div className={styles.element}>
                              <img className={styles.img} src={WorkSwitch} alt={""}/>
                              <div className={styles.element_text}>System proceeds exchange</div>
                          </div>
                          <img className={styles.line} src={WorkLine} alt={""}/>
                          <div className={styles.element}>
                              <img className={styles.img} src={WorkSquad} alt={""}/>
                              <div className={styles.element_text}>Fiat goes to your account</div>
                          </div>
                      </div>
                  :
                      <div className={styles.element_block}>
                          <div className={styles.element}>
                              <img className={styles.img} src={WorkCrypto} alt={""}/>
                              <div className={styles.element_text}>Customer chooses the coin</div>
                          </div>
                          <img className={styles.line} src={WorkLine} alt={""}/>
                          <div className={styles.element}>
                              <img className={styles.img} src={WorkMoney} alt={""}/>
                              <div className={styles.element_text}>Pay fiat</div>
                          </div>
                          <img className={styles.line} src={WorkLine} alt={""}/>
                          <div className={styles.element}>
                              <img className={styles.img} src={WorkSwitchSecond} alt={""}/>
                              <div className={styles.element_text}>System proceeds exchange</div>
                          </div>
                          <img className={styles.line} src={WorkLine} alt={""}/>
                          <div className={styles.element}>
                              <img className={styles.img} src={WorkCryptoSecond} alt={""}/>
                              <div className={styles.element_text}>Crypto goes to your wallet</div>
                          </div>
                      </div>
                  }
              </div>
          </section>
        )
    }
}
export default WorksSection;
