import React from "react";
import styles from './index.module.scss'
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import { formApi } from "../../api/form";

const EmailRegEx = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
const UrlRegEx = /^(http|https):\/\/[^ "]+$/;

class Footer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            website: '',
            text: '',
            errorName: "",
            errorEmail: "",
            errorWebsite: "", 
            showModal: false,
        }
    }
    onChangeInfo = (value, input) => {
        switch (input) {
            case 'name':
                this.setState({ name: value });
                this.validateName(value, false);
                break;
            case 'email':
                this.setState({ email: value });
                this.validateEmail(value, false);
                break;
            case 'website':
                this.setState({ website: value });
                this.validateWebsite(value, false);
                break;
            case 'text':
                this.setState({ text: value });
                break;
            default:
                return null;
        }
        this.validate(false);
    };
    validate = (value, name, force, validator) => {

        let error = "";

        if(validator) {
            error = validator(value)
        }

        const field = `error${name}`;

        const result = {};

        if(error === "") {
            result[field] = "";
        } else {
            if(force) {
                result[field] = error;
            }
        }

        this.setState(result);

        return error === "";
    }
    validateName = (value, force) => this.validate(value, "Name", force, 
        value => value === "" 
            ? "Error: Your name is required" 
            : "");
    validateEmail = (value, force) => this.validate(value, "Email", force, 
        value => !EmailRegEx.test(value) 
            ? "Error: Valid email address is required" 
            : "");
    validateWebsite = (value, force) => this.validate(value, "Website", force, 
        value => value !== "" && !UrlRegEx.test(value) 
            ? "Error: valid url is required" 
            : "");
    onCloseModal = () => {
        this.setState({ 
            showModal: false,
            name: "",
            email: "",
            website: "",
            text: "" 
        });
    };
    onSubmit = () => {

        let valid = true;
        valid = this.validateName(this.state.name, true) && valid;
        valid = this.validateEmail(this.state.email, true) && valid;
        valid = this.validateWebsite(this.state.website, true) && valid;

        if(!valid) {
            return;
        }

        const params = {
            name: this.state.name,
            email: this.state.email,
            website: this.state.website,
            text: this.state.text,
        };
        formApi(params)
            .then(res => this.setState({ 
                showModal: true
            }))
            .catch(err => {
                console.log(err);
            });
    };

    render() {
        return (
            <footer className={styles.footer}>
                <a href={'!#'} name="contacts"> </a>
                <div className={styles.footer_block}>
                    <div className={styles.form_block}>
                        <div className={styles.caption}>Contact us:</div>
                        <div className={[
                            styles.input_block, 
                            styles.name,
                            this.state.name === "" ? `${styles.input_block_empty}` : ``,
                            this.state.errorName !== "" ? `${styles.input_block_error}` : ``,
                        ].join(" ")}>
                            <input className={styles.input} type="text" placeholder="Your Name" value={this.state.name}
                                onChange={(event) => this.onChangeInfo(event.target.value, 'name')} />
                            <div className={styles.error}>{this.state.errorName}</div>
                        </div>
                        <div className={styles.separator}></div>
                        <div className={[
                            styles.input_block, 
                            styles.email,
                            this.state.email === "" ? `${styles.input_block_empty}` : ``,
                            this.state.errorEmail !== "" ? `${styles.input_block_error}` : ``,
                        ].join(" ")}>
                            <input className={styles.input} type="email" placeholder="E-mail" value={this.state.email}
                                onChange={(event) => this.onChangeInfo(event.target.value, 'email')} />
                            <div className={styles.error}>{this.state.errorEmail}</div>
                        </div>
                        <div className={styles.separator}></div>
                        <div className={[
                            styles.input_block, 
                            styles.company,
                            this.state.name === "" ? `${styles.input_block_empty}` : ``,
                            this.state.errorWebsite !== "" ? `${styles.input_block_error}` : ``,
                        ].join(" ")}>
                            <input className={styles.input} type="text" placeholder="Link to Your Website or Mobile App" value={this.state.website}
                                onChange={(event) => this.onChangeInfo(event.target.value, 'website')} />
                            <div className={styles.error}>{this.state.errorWebsite}</div>
                        </div>
                        <div className={styles.separator}></div>
                        <div className={[
                            styles.textarea_block,
                            this.state.text === "" ? `${styles.textarea_block_empty}` : ``,
                        ].join(" ")}>
                            <textarea className={styles.textarea_text} placeholder="Your message" value={this.state.text}
                                onChange={(event) => this.onChangeInfo(event.target.value, 'text')}>
                            </textarea>
                        </div>
                        <button tabIndex="0" className={styles.button} onClick={this.onSubmit}>Send message</button>
                    </div>
                    <div className={styles.footer_info}>Â© 2020 Crypto Pay. Trademarks belong to their respective owners.
                    All rights reserved
                  </div>
                    <Modal show={this.state.showModal} onHide={this.onCloseModal}>
                        <Modal.Header closeButton>
                            <Modal.Title>Thanks!</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>We will contact you as soon as possible.</Modal.Body>
                        <Modal.Footer>
                            <Button variant="primary" onClick={this.onCloseModal}>
                                Close
                            </Button>
                        </Modal.Footer>
                    </Modal>
                </div>
            </footer>
        )
    }
}
export default Footer;
